<template>

  <div>
    
    <van-nav-bar
  title="背忘录"
 
/>


    <div>
      <!-- <h1 style="background-color:#5592FA">背忘录</h1 > -->

    <h3 style="position auto">
      欢迎你
      {{this.$store.state.nickname}}
    </h3>

    <van-row type="flex"
      justify="center">
      <div id="image">

        <van-image
          width="20rem"
          span="22"
          height="15rem"
          fit="contain"
          :src="src">

          <template
            v-slot:loading>
            <van-loading
              type="spinner"
              size="20"
              color="#1989fa" />
          </template>
        </van-image>

      </div>
    </van-row>

    <van-row>
      <p>
        {{data.sentence}}
      </p>
      <p
        style="position:absolute;right: 1vh">
        ------{{data.author}}
      </p>
    </van-row>

    <!-- 两端对齐 -->

    <van-row type="flex"
      justify="space-between"
      class="btns1">
      <van-button round
        type="info" span="8"
        plain
        @click="startAddGroup">
        录入句子或短语
      </van-button>

    </van-row>
    <van-row type="flex"
      justify="space-between"
      class="btns2">
      <van-button round
        type="info" span="8"
        plain
        @click="startAdd"
        >
        录入单词
      </van-button>
      <van-button round
        span="8" type="info"
        plain
        @click="startRecite">
        开始背单词
      </van-button>
    </van-row>
    </div>

  </div>
</template>

<script>
import axios from 'axios'

export default {
  components: {},
  methods: {
    /**
     * 先请求单词数据并携带数据再做跳转
     */
    startRecite() {
      axios({
        method: 'post',
        url: this.$baseURL+'/word/todayWords',
        data: {
          uid: this.$store.state.uid,
        },
      }).then((res) => {
        // console.log(res.data)
        this.$router.push({
          path: '/phone/reciteWords',
          query: { data: res.data.data },
        })
        if (
          res.data.data.length == 0 &&
          !(
            this.$store.state.uid == null ||
            this.$store.state.uid == '' ||
            this.$store.state.uid === undefined
          )
        ) {
          alert('无数据,请先去添加')
        }
      })
    },

    startAdd() {
      this.$router.push('/phone/addWords')
    },
    startAddGroup() {
      this.$router.push('/phone/addGroup')
    },
  },
  name: 'MyIndex',
  data() {
    return {
      data: '',
      src: '',
      fit: 'contain',
    }
  },
  mounted() {
    if (
      this.$store.state.uid == null ||
      this.$store.state.uid == '' ||
      this.$store.state.uid === undefined
    ) {
      this.$router.push('/phone/login')
    }

    axios({
      method: 'post',
      url: this.$baseURL+'/getOne',
      data: {},
    }).then((res) => {
      this.data = res.data.data
      this.src = this.data.pic
    })
  },
}
</script>
<style scoped>
.btns1 {
  height: 70px;
}
.btns2 {
  height: 70px;
}

</style>